
export default {
    '/signin':{
        component (resolve) {
            require(['components/login/signin.vue'], resolve)
        }
    },
    '/signup':{
        component (resolve) {
            require(['components/login/signup.vue'], resolve)
        }
    },
    '/':{
        component (resolve) {
            require(['components/main/content.vue'], resolve)
        }
    }
}


